.swag-select {
    display: block;
    // font-size: 1em;
    font-family: sans-serif;
    font-weight: 700;
    color: #444;
    line-height: 2em;
    padding: 0em 2.5em 0em 1em;
    box-sizing: border-box;
    margin: 0;
    border: 1px solid #aaa;
    box-shadow: 0 1px 0 1px rgba(0,0,0,.04);
    border-radius: 6px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-color: #fff;
    background-image: url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23007CB2%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E'),
      linear-gradient(to bottom, #ffffff 0%,#e5e5e5 100%);
    background-repeat: no-repeat, repeat;
    background-position: right 1em top 50%, 0 0;
    background-size: 1em auto, 100%;
    margin: 0em 1em;
}
.swag-select::-ms-expand {
    display: none;
}
.swag-select:hover {
    border-color: #888;
}
.swag-select:focus {
    border-color: #aaa;
    box-shadow: 0 0 1px 3px rgba(59, 153, 252, .7);
    box-shadow: 0 0 0 3px -moz-mac-focusring;
    color: #222;
    outline: none;
}
.swag-select option {
    font-weight:normal;
}

.branding-logo-sw {
  background-image: url('./images/shockwave-logo.svg');
}

.branding-logo-ag {
  background-image: url('./images/addictinggames-logo.svg');
}

.swag-button-confirmation {
  background-color: #0163a0;
  width: 4em;
  padding: 1em 1em;
  color: #fff;
  border-radius: 6px;
  text-align: center;
  cursor: pointer;

  &:hover {
    background-color: darken( #0163a0, 10% );
  }

}

